@import "includes/grids";

//base
.container {
	@include container;
	@include r(1200) {
		padding: 0 10px; }

	&_md {
		max-width: 850px;
		width: 100%;
		margin: 0 auto;
		@include r(480) {
			padding: 0 10px; } }
	&_sm {
		max-width: 670px;
		width: 100%;
		margin: 0 auto;
		@include r(480) {
			padding: 0 10px; } } }


//Header
.callback {
	@include span(4); }

.logo {
	@include span(4); }

.action {
	@include span(4);
	margin-right: 0; }

//Footer
.footer-item {
	@include span(2);
	&_logo {
		@include span(3); }
	&_social {
		@include span(3);
		margin-right: 0; }

	@include r(768) {
		margin: 0 auto 30px;
		@include span(4);
		&:last-child {
			margin-right: 0; }
		&_logo {
			@include span(6); }
		&_info {
			order: 3; }
		&_buyers {
			order: 4; }
		&_comp {
			order: 5;
			margin-right: 0; }
		&_social {
			order: 2;
			@include span(6);
			margin-right: 0; } }
	@include r(480) {
		width: 100%;
		text-align: center;
		margin-right: 0;
		margin-bottom: 0;
		&_info {
			order: 2; }
		&_buyers {
			order: 3; }
		&_comp {
			order: 4;
			margin-bottom: 50px;
			margin-right: initial; }
		&_social {
			order: 1;
			margin-right: initial;
			margin-bottom: 50px; }
		&_logo {
			order: 5;
			margin-bottom: 0; } } }

//Catalog Item
.product-item {
	width: 25%;
	@include r(1024) {
		@include span(4);
		margin-bottom: gutter();
		&:nth-child(3n) {
			margin-right: 0; } }
	@include r(480) {
		width: 318px !important;
		margin: 0 auto 20px;
		&:nth-child(3n) {
			margin-right: auto; }
		&:last-child {
			margin-bottom: 0; } } }

//Product
.product {
	&-img {
		@include span(6);
		@include r(480) {
			width: 100%; } }
	&-text {
		@include span(6);
		margin-right: 0;
		@include r(480) {
			width: 100%; } } }

.product-desc__item {
	width: 40%;
	float: left;
	margin-right: 8%;
	&:nth-child(2) {
		width: 29%;
		margin-right: 30px;
		float: left; }
	&:nth-child(3) {
		width: 20%;
		max-width: 250px;
		float: left;
		margin-right: 0; }
	@include r(1024) {
		margin-right: 5%;
		&:nth-child(2) {
			width: 25%; } }
	@include r(768) {
		width: 100%;
		margin-right: gutter();
		margin-bottom: 30px;
		&:nth-child(2) {
			@include span(6); }
		&:nth-child(3) {
			@include span(6);
			margin-right: 0; } }
	@include r(480) {
		&:nth-child(2) {
			@include span(full); }
		&:nth-child(3) {
			@include span(full);
			margin-right: gutter(); } }

	@include r(375) {
		&:nth-child(2) {
			@include span(full); }
		&:nth-child(3) {
			@include span(full); } } }

//advantages
.advantages__item {
	@include span(3);
	&:last-child {
		margin-right: 0; }
	@include r(480) {
		@include span(6);
		margin-bottom: 170px;
		&:last-child {
			margin-right: gutter();
			margin-bottom: 0; }
		&:nth-child(2n) {
			margin-right: 0; }
		&:nth-child(3) {
			margin-bottom: 0; } } }

//recently
.recently {
	&__item {
		@include span(3);
		&:last-child {
			margin-right: 0; }
		@include r(480) {
			@include span(6);
			margin-bottom: 30px;
			&:nth-child(3n) {
				margin-right: 0; } }
		@include r(320) {
			width: 100%;
			&:nth-child(2n) {
				margin-right: gutter(); } } } }

//news-item
.news-item {
	@include span(6);
	&:last-child {
		margin-right: 0; }
	@include r(480) {
		@include span(full);
		&:last-child {
			margin-right: gutter(); } }
	//news-item_small
	&_small {
		@include span(3);
		&:last-child {
			margin-right: 0; }
		@include r(768) {
			@include span(4);
			&:nth-child(3n) {
				margin-right: 0; } }
		@include r(480) {
			@include span(6);
			&:nth-child(3n) {
				margin-right: gutter(); }
			&:nth-child(2n) {
				margin-right: 0; } }
		@include r(375) {
			@include span(full);
			&:nth-child(2n) {
				margin-right: gutter(); } } } }
