//home
.home {
	padding-bottom: 50px;
	.product-item {
		padding: 50px;
		@include tr(.35s);
		@include span(4);
		&:hover {
			background-color: $color-bg--lk;
			border-color: transparent; }
		&:nth-child(3n) {
			margin-right: 0; }
		@include r(768) {
			padding: 15px;
			border: none; }
		@include r(480) {
			@include span(6);
			padding-bottom: 0;
			margin: 0 auto 30px;
			&:nth-child(2n) {
				margin: 0 auto 30px; }
			&:nth-child(3n) {
				margin: 0 auto 30px; }
			&:last-child {
				margin-bottom: 0; } }
		&__wrap {
			margin-bottom: 40px;
			@include r(480) {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				align-items: center;
				align-content: center; } }
		&__title {
			color: $color-text;
			font-family: encore-50, sans-serif;
			font-size: rem(28);
			text-transform: uppercase;
			letter-spacing: 2.17px;
			@include r(425) {
				font-family: $f-mobile-light; } }
		&__button {
			opacity: 1;
			text-align: center; } }
	.slider__link {
		left: auto;
		@include r(480) {
			padding: 0 30px; }
		&::after {
			transform: translateY(-38%); } } }

//home-item
.home-item {
	padding: 25px;
	text-align: center;
	@include r(375) {
		padding: 25px 0; }
	//home-item__wrap
	&__wrap {
		display: flex;
		margin-bottom: 50px;
		@include r(768) {
			flex-wrap: wrap; }
		@include r(480) {
			margin-bottom: 20px; }
		&_left {
			display: flex;
			width: 56%;
			align-items: flex-end;
			padding: 350px 25px 25px;
			text-align: center;
			margin-right: 2%;
			background-color: #f2f6ff;
			background-position: top left;
			background-repeat: no-repeat;
			@include r(768) {
				width: 100%;
				margin-right: 0; }
			@include r(480) {
				flex-wrap: wrap;
				margin-bottom: 20px; }
			@include r(320) {
				padding: 320px 0 25px; }
			.home-item {
				width: 50%;
				display: flex;
				flex-direction: column;
				position: relative;
				&:nth-child(2) {
					&::before {
						content: '. . . . .';
						display: block;
						position: absolute;
						top: 16%;
						left: -33px;
						@include rotate(90); } }
				@include r(480) {
					width: 100%;
					&:nth-child(2) {
						border-top: 1px solid $color-border;
						&::before {
							display: none; } } }

				&__title {
					margin-bottom: 40px; }
				&__desc {
					margin-bottom: 63px; }
				.btn {
					&::after {
						right: 80px; }
					&:hover {
						&::after {
							right: 70px; } }
					@include r(1024) {
						&::after {
							right: 55px; }
						&:hover {
							&::after {
								right: 65px; } } } } } }

		&_right {
			display: flex;
			flex-direction: column;
			width: 42%;
			padding: 25px;
			text-align: center;
			background-color: #f5f5f5;
			@include r(768) {
				width: 70%;
				margin: 30px auto; }
			@include r(480) {
				width: 100%; }
			@include r(320) {
				padding: 25px 0; }
			.home-item {
				display: flex;
				align-items: center;
				&:nth-child(2n) {
					flex-direction: row-reverse; }
				&__desc {
					span {
						color: $color-text;
						font-family: encore-100, sans-serif;
						font-size: rem(24);
						@include r(425) {
							font-family: $f-mobile-light;
							font-size: rem(20); }
						@include r(375) {
							font-size: rem(20); } } }
				.btn {
					&::after {
						transform: translateY(-41%); } } } } }
	//home-item__title
	&__title {
		color: $color-text;
		font-family: encore-100, sans-serif;
		font-size: rem(36);
		text-transform: uppercase;
		letter-spacing: 2.61px;
		span {
			display: block; } }
	//home-item__subtitle
	&__subtitle {
		display: block;
		margin-bottom: 15px;
		color: $color-accent;
		font-family: encore-50, sans-serif;
		font-size: rem(24); }
	//home-item__desc
	&__desc {
		display: block;
		color: #6c6c6c;
		font-size: rem(15); }
 }	//home-item__img

//brands
.brands {
	padding: 30px 0 50px; }

//home-section
.home-section {
	position: relative;
	min-height: 500px;
	margin-bottom: 50px;
	@include cover;
	//home-section__text
	&__text {
		@include vy;
		left: 60px;
		width: 350px;
		text-align: center;
		@include r(480) {
			padding-top: 15px;
			position: relative;
			margin: 0 auto;
			top: auto;
			left: auto;
			@include tY(70%); } }
	//home-section__title
	&__title {
		display: block;
		margin-bottom: 42px;
		font-family: encore-50, sans-serif;
		font-size: rem(36);
		text-transform: uppercase;
		text-align: center;
		letter-spacing: 2.61px;
		position: relative;
		@include r(768) {
			font-size: rem(28); }
		&::after {
			content: '. . . . .';
			color: $color-text-light;
			display: block;
			@include vx;
			bottom: -35px; }
		span {
			display: block; } }
	//home-section__link
	&__link {
		border: none;
		background-color: transparent;
		color: $color-text-light;
		text-transform: initial;
		min-width: auto;
		padding-right: 35px;
		position: relative;
		&:hover {
			background-color: transparent;
			&::after {
				right: 5px; } }
		&::after {
			top: 53%; } } }
