.footer {
	padding: 80px 0;
	position: relative;
	background-image: url("../img/general/footer-bg.jpg");
	background-size: cover;
	background-position: top center;
	@include r(480) {
		padding: 50px 0; }
	//footer__logo
	&__logo {
		float: none;
		width: 100%;
		margin-right: auto;
		text-align: left;
		@include r(480) {
			text-align: center; }
		.logo__link {
			.icon {
				fill: rgba(#fff, .7);
				width: 200px;
				height: 55px; } } }

	&__logo {
		display: block;
		font-size: 26px;
		text-transform: uppercase;
		font-family: oswald-300;
		letter-spacing: 2px; }

	&__desc {
		display: block;
		font-size: rem(13);
		color: 555;
		span {
			display: block;
			font-size: rem(15);
			line-height: 30px;
			color: #444444; } }
	&__phone {
		display: block;
		margin-top: 15px;
		letter-spacing: 3.6px; } }

.footer-item {
	.h5 {
		padding-top: 13px;
		padding-bottom: 11px;
		font-family: oswald-400, sans-serif;
		position: relative;
		&::after {
			display: none; }
		@include r(480) {
			display: inline-block;
			&::after {
				display: block;
				content: '\f107';
				font-family: FontAwesome;
				color: $color-brown;
				@include vy;
				right: -25px; } } }
	&__wrap {
		@include r(768) {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;
			flex-flow: wrap;
			align-content: space-between; } }

	&_social {
		text-align: center; } }


.footer-list {
	&__link {
		display: block;
		margin-top: 18px;
		font-family: encore-300;
		font-size: rem(15);
		color: $color-text-bg-dark;
		&::last-child {
			margin-top: 0; } } }

.social {
	&__item {
		&_text {
			& + & {
				margin-left: 24px; } } } }

