//catalog
.catalog {
	.breadcrumbs {
		&__item {
			@include r(480) {
				display: none; } } } }

//sorting
.sorting {
	//sorting__wrap
	&__wrap {
		text-align: center;
		position: relative;
		padding: 15px 0;
		@include clr;
		@include r(768) {
			display: none; } }
	//sorting__item
	&__item {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		color: $color-text-light;
		cursor: pointer;
		@include tr(.35s);

		& + & {
			margin-left: 50px;
			@include r(480) {
				margin-left: 0;
				margin-right: 25px; } }
		&:hover {
			color: $color-brown; }
		&.is-active {
			color: $color-brown;
			&::after {
				content: '\f0d7';
				font-family: FontAwesome;
				font-size: 16px;
				color: $color-brown;
				display: inline-block;
				position: absolute;
				top: 1px;
				right: -15px; } }
		//sorting__item_title
		&_title {
			color: $color-text;
			@include r(480) {
				display: block; } } }
	//sorting__btn
	&__btn {
		display: inline-block;
		vertical-align: middle;
		padding-right: 30px;
		position: absolute;
		top: 0;
		right: 0;
		&::after {
			content: '';
			display: inline-block;
			vertical-align: middle;
			position: absolute;
			top: 6px;
			right: 0;
			@include s(filtr__btn); } } }

//Catalog Item
.catalog {
	&__seo-text {
		margin-top: 100px;
		@include r(480) {
			margin-top: 50px; } } }

.product-item {
	padding: 25px 30px 25px 30px;
	border: 1px solid transparent;
	@include tr(0.35s);

	&:hover {
		border: 1px solid $color-brown-lighten;
		background-color: #fff;
		.product-item__button {
			opacity: 1; } }

	@include r(768) {
		padding: 14px; }

	@include r(480) {
		padding: 20px 30px; }

	@include r(375) {
		padding: 15px 15px 30px; }

	&__img {
		margin-bottom: 5px;
		img {
			margin: 0 auto;
			display: block; } }
	//product-item__title
	&__title {
		display: block;
		margin-bottom: 15px;
		text-align: center;
		color: $color-text;
		font-family: encore-200;
		font-size: rem(16);
		a {
			color: $color-text; }
		@include r(425) {
			font-family: $f-mobile-light; } }
	//product-item__price
	&__price {
		display: block;
		margin-bottom: 40px;
		font-family: oswald-300;
		font-size: rem(18);
		text-align: center;
		color: $color-accent;
		position: relative;
		@include clr;
		@include r(768) {
			margin-bottom: 20px; }
		@include r(480) {
			margin-bottom: 40px; }
		@include r(414) {
			margin-bottom: 15px; }
		&_old {
			font-family: oswald-300;
			font-size: rem(14);
			text-decoration: line-through;
			color: $color-text-light;
			position: absolute;
			top: 4px;
			left: 0; }
		&_sale {
			padding: 7px 10px;
			color: $color-accent;
			font-family: oswald-400;
			font-size: rem(11);
			position: absolute;
			top: 4px;
			right: 0;
			z-index: 91;
			&::after {
				content: '';
				display: block;
				position: absolute;
				top: 2px;
				right: 0;
				@include s(sale_clean);
				z-index: -1; } } }
	//product-item__button
	&__button {
		opacity: 0;
		text-align: center;
		@include tr(.35s);
		@include r(768) {
			opacity: 1;
			.btn {
				&_add {
					font-size: rem(10); }
				&_fav {
					width: 44px;
					height: 44px; } } }
		@include r(480) {
			text-align: center;
			.btn {
				&_add {
					font-size: rem(13); }
				&_fav {
					width: 47px;
					height: 47px;
					position: relative;
					top: -2px; } } } } }

//seo-text
.seo-text {
	text-align: center;
	padding-bottom: 70px;
	&__title {
		margin-bottom: 20px;
		color: #474747;
		font-family: encore-50, sans-serif;
		font-size: rem(36);
		@include r(425) {
			font-family: $f-mobile-light;
			font-size: rem(32); } }
	&__desc {
		display: block;
		margin-bottom: 25px;
		position: relative;
		max-width: 950px;
		overflow: hidden;
		margin: 0 auto;
		color: $color-text;
		font-family: encore-300, sans-serif;
		font-size: rem(16);
		@include r(425) {
			font-family: $f-mobile; } } }

