//color link
@mixin links($link, $visit, $hover, $active) {
	a {
		color: $link;
		&:visited {
			color: $visit; }
		&:hover {
			color: $hover; }
		&:active {
			color: $active; } } }

//transition
@mixin tr($time) {
	-webkit-transition: all $time ease;
	-moz-transition: all $time ease;
	-ms-transition: all $time ease;
	-o-transition: all $time ease;
	transition: all $time ease; }

//border-radius
@mixin br($radius) {
	-webkit-border-radius: $radius;
	-moz-border-radius: $radius;
	-ms-border-radius: $radius;
	-o-border-radius: $radius;
	border-radius: $radius; }

//box-shadow
@mixin bs($shadow) {
	-webkit-box-shadow: $shadow;
	-moz-box-shadow: $shadow;
	-ms-box-shadow: $shadow;
	-o-box-shadow: $shadow;
	box-shadow: $shadow; }

//text-shadow
@mixin ts($string) {
	-webkit-text-shadow: $string;
	-moz-text-shadow: $string;
	-ms-text-shadow: $string;
	-o-text-shadow: $string;
	text-shadow: $string; }


@function em($size, $context: 16) {
	@return ($size/$context) * 1em; }

@function img-url($image) {
	@return url('../img/#{$image}'); }

@mixin image($image) {
	background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
	transition: $what $dur $easing; }

@mixin smooth-scroll {
	-webkit-overflow-scrolling: touch; }

@mixin smooth-font {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@mixin gpu {
	transform: translate3d(0,0,0); }

@mixin md {
	@include r(1023) {
		@content; } }

@mixin sm {
	@include r(767) {
		@content; } }

@mixin xs {
	@include r(599) {
		@content; } }

@mixin tY($var) {
	transform: translateY($var); }

@mixin tX($var) {
	transform: translateX($var); }
