.error {
	padding-bottom: 80px;
	text-align: center;
	&__img {
		position: relative;
		margin-bottom: 50px;
		@include r(480) {
			overflow: hidden; }
		img {
			margin: 0 auto 0 39%;
			width: auto;
			@include r(1024) {
				margin-left: 37%; }
			@include r(768) {
				margin-left: 33%; }
			@include r(480) {
				margin-left: 25.5%; } } }
	&__num {
		color: $color-brown;
		font-family: encore-hl, sans-serif;
		font-size: rem(166);
		line-height: 26.13px;
		position: absolute;
		bottom: 29%;
		@include r(375) {
			font-size: rem(118); }
		&_left {
			left: 36.5%;
			@include r(1024) {
				left: 35%; }
			@include r(768) {
				left: 30%; }
			@include r(480) {
				left: 19%; } }
		&_right {
			right: 34.5%;
			@include r(1024) {
				right: 31.5%; }
			@include r(768) {
				right: 25%; }
			@include r(480) {
				right: 20%; } } }
	&__desc {
		color: #606060; }
	&__link {
		color: $color-accent;
		&:hover {
			text-decoration: underline;
			color: $color-accent; } } }
