//Fonts
$base-font-value: 16;
@function rem($x) {
	@return ($x / $base-font-value)+rem; }

$base-font-size  : 16px;
$base-font-family: encore-300, sans-serif;

$f-mobile: opsans-400, sans-serif;
$f-mobile-light: opsans-300, sans-serif;

//Colors
$color-accent         :          #ee8b76;
$color-accent-two     :          #5b98aa;

$color-text           :          #616161;

$color-text-light     :          #9e9e9e;
$color-text-lighten   :          #b8b8b8;
$color-text-bg-dark   :          #888888;
$color-text-btn       :          #3b3629;

$color-bg-darken      :          #232323;
$color-bg-dark        :          #222222;
$color-bg-dark-lighten:          #2a2a2a;

$color-brown          :          #baab82;
$color-brown-lighten  :          #f7edbc;

$color-border         :          #ebebeb;
$color-border-bg--dark:          #877c5f;
$color-input-bg--dark :          #1c1c1c;

$color-bg--lk         :          #fcfaec;
