.basket, .lk {
	//.tab
	.tab {
		display: inline-block;
		padding: 15px 50px 8px;
		margin: 0 20px;
		font-family: oswald-300, sans-serif;
		font-size: rem(16);
		text-transform: uppercase;
		letter-spacing: 1.2px;
		color: $color-brown;
		cursor: pointer;
		border: 1px solid transparent;
		@include nosel;
		position: relative;
		@include tr(.35s);
		&::after {
			display: block;
			content: '\f178';
			font-family: FontAwesome;
			font-size: 16px;
			color: $color-brown;
			position: absolute;
			top: 15px;
			left: 15px;
			@include tr(.35s); }
		&::before {
			display: none;
			content: '';
			height: 1px;
			width: 100%;
			background-color: #FFF;
			position: absolute;
			left: 0;
			bottom: -2px; }
		&:hover {
			color: $color-accent;
			&::after {
				left: 20px;
				color: $color-accent; } }
		@include r(1200) {
			&::after {
				top: 15px; }
			&::before {
				bottom: -2px; } }
		@include r(768) {
			padding: 12px 25px 6px;
			font-size: rem(15);
			&::after {
				top: 11px;
				left: 0; }
			&:hover {
				&::after {
					left: 10px; } } }
		@include r(480) {
			&::after {
				display: none; } }
		@include r(375) {
			padding: 12px 23px 6px;
			margin: 0 11px; }

		&.is-active {
			color: $color-accent;
			border: 1px solid $color-border;
			border-bottom-color: transparent;
			&::after {
				display: none; }
			&::before {
				display: block; } }
		span {
			@include r(480) {
				display: none; } }
		//.tab .icon
		.icon {
			width: 23px;
			height: 23px;
			fill: $color-brown;
			position: relative;
			@include rmin(481) {
				display: none; }
			&-address {
				top: 1px; } }
		//.tab__wrap
		&__wrap {
			margin-bottom: 35px;
			padding-left: 67px;
			text-align: center;
			border-bottom: 1px solid $color-border;

			@include r(480) {
				padding-left: 0; } }
		//.tab-item
		&-item {
			margin-bottom: 70px;
			&__top {
				padding-bottom: 50px;
				border-bottom: 1px solid $color-border;
				@include clr;
				@include r(480) {
					padding-bottom: 0;
					border-bottom: none; } }
			//.tab-item__bottom
			&__bottom {
				padding-top: 50px;
				display: flex;
				justify-content: space-between;
				align-items: center;
				@include r(1024) {
					flex-wrap: wrap; }
				@include r(425) {
					padding-top: 20px; }
				.btn_trans, .btn_checkout {
					@include r(1024) {
						margin: 30px auto 0; }
					@include r(320) {
						padding: 15px 40px 15px 15px; } } } } }
	//.basket .price, lk .price
	.price {
		color: $color-accent;
		font-family: oswald-300, sans-serif;
		font-size: rem(18);
		&__old {
			margin-right: 20px;
			color: $color-text-light;
			font-family: oswald-300, sans-serif;
			font-size: rem(14);
			text-decoration: line-through; } } }

.basket {
	//.basket__main
	&__main {
		float: left;
		width: calc(100% - 360px);
		margin-right: 60px;
		@include r(1024) {
			width: 100%;
			float: none; } }
	//.basket__asside
	&__asside {
		float: right;
		width: 300px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		@include r(1024) {
			display: none; }
		&_link {
			display: inline-block;
			margin: 0 auto;
			margin-top: 25px;
			text-align: center;
			font-size: rem(14);
			color: rgba($color-text, .7);
			border-bottom: 1px solid rgba($color-text, .7);
			&:hover {
				border-color: $color-brown; } } }
	//.basket.product-item
	.product-item {
		float: none;
		width: 100%;
		border: 1px solid $color-border;
		&__button {
			opacity: 1; } }
	//.basket .price
	.price {}
	color: $color-accent;
	font-family: oswald-300, sans-serif;
	font-size: rem(18);
	&__old {
		margin-right: 20px;
		color: $color-text-light;
		font-family: oswald-300, sans-serif;
		font-size: rem(14);
		text-decoration: line-through; } }

//.basket-item
.basket-item {
	margin-bottom: 30px;
	display: flex;
	align-items: center;
	@include clr;
	&:last-child {
		margin-bottom: 0; }
	@include r(480) {
		flex-wrap: wrap;
		padding-bottom: 20px;
		border-bottom: 1px solid $color-border; }
	//.basket-item__img
	&__img {
		margin-right: 30px;
		width: 102px;
		height: 102px;
		@include r(375) {
			margin-right: 10px; }
		@include r(320) {
			margin: 0 auto 15px; }
		img {
			@extend %i; } }
	//.basket-item__title
	&__title {
		display: block;
		color: $color-text;
		font-family: encore-300, sans-serif;
		font-size: rem(16);
		max-width: 250px;
		&:hover {
			color: $color-accent; }
		@include r(375) {
			max-width: 240px; }
		@include r(320) {
			max-width: 100%;
			margin: auto;
			text-align: center; } }
	//.basket-item__articul
	&__articul {
		display: block;
		color: $color-text-light; }
	//.basket-item__price
	&__price {
		margin: 0 45px 0 auto;
		@include r(480) {
			margin: 15px auto 0; }
		@include r(320) {
			margin: 15px 15px 0 15px; } }
	//.basket-item .size
	.size {
		margin-bottom: 0;
		margin-left: auto;
		@include r(480) {
			margin-top: 15px; }
		&__title {
			margin-right: 10px;
			color: $color-text;
			font-family: oswald-300, sans-serif;
			font-size: rem(16); }
		//.basket-item .size .extra-item
		.extra-item {
			position: relative;
			padding-right: 12px;
			&::after {
				content: '\f107';
				display: block;
				font-family: FontAwesome;
				font-size: 14px;
				color: $color-text-light;
				position: absolute;
				top: 1px;
				right: 0; }
			&__dropdown {
				@include r(480) {
					left: -200%;
					&::before {
						left: auto;
						right: 10px; } }
				@include r(375) {
					left: 0;
					&::before {
						right: auto;
						left: 10px; } } } } }
	//.basket-item .sa-close
	.sa-close {
		@include r(320) {
			margin: 15px 0 0 auto; } } }

//.promokod
.promokod {
	display: flex;
	width: 310px;
	align-content: center;
	justify-content: space-between;
	flex-wrap: wrap;
	@include r(768) {
		flex-basis: 310px;
		text-align: center; }
	@include r(480) {
		margin: 0 auto 30px; }
	&__text {
		padding-bottom: 15px;
		margin: auto;
		text-align: center;
		color: $color-text-light;
		font-family: encore-100, sans-serif;
		font-size: rem(22);
		@include r(768) {
			width: 100%; } }
	&__box {
		display: flex; }
	.sa-input {
		padding: 15px 15px 10px;
		max-width: 150px;
		&__box {
			margin-right: 20px;
			&.is-focus {
				.sa-input__placeholder {
					top: 0; } } }
		&__placeholder {
			top: 18px;
			font-size: rem(16); } } }

//.itog
.itog {
	position: relative;
	@include r(480) {
		margin: 0 auto; }
	&::before {
		content: '';
		display: block;
		position: absolute;
		top: -27px;
		left: -60px;
		@include s(price-border); }
	@include r(1024) {
		&::before {
			display: none; } }
	//.itog__text
	&__text {
		color: #474747;
		font-family: encore-100, sans-serif;
		font-size: rem(28); }
	&__num {
		color: $color-accent;
		font-family: oswald-500, sans-serif;
		font-size: rem(18); } }

//.pay-item
.pay-item {
	width: 300px;
	margin-bottom: 35px;
	padding: 50px 35px 30px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	border: 1px solid $color-border;
	//.pay-item__wrap
	&__wrap {
		display: flex;
		justify-content: space-around;
		align-items: stretch;
		flex-wrap: wrap; }
	//.pay-item__title
	&__title {
		margin-bottom: 35px;
		color: #474747;
		font-family: encore-100, sans-serif;
		font-size: rem(30); }
	//.pay-item__desc
	&__desc {
		color: $color-text-light;
		font-family: encore-300, sans-serif;
		font-size: rem(15); }
	//.pay-item__itog
	&__itog {
		display: block;
		margin-bottom: 15px;
		span {
			color: $color-text;
			font-family: oswald-300, sans-serif;
			font-size: rem(15); } }
	//.pay-item__delivery
	&__delivery {
		display: block;
		margin-bottom: 15px;
		span {
			color: $color-text;
			font-family: encore-600, sans-serif; } }
	//.pay-item__sale
	&__sale {
		position: relative;
		left: -35px;
		display: block;
		margin-bottom: 15px;
		.product-item__price_sale {
			top: 0;
			right: -10px; } }
	//.pay-item__sum
	&__sum {
		margin: auto auto 15px;
		color: $color-text-light;
		font-size: rem(15);
		span {
			display: block;
			color: $color-accent;
			font-family: oswald-500, sans-serif;
			font-size: rem(24); } } }

//.form-delivery
.form-delivery, .form-my-data {
	//.form-delivery__inner
	&__inner {
		display: flex;
		flex-wrap: wrap; }
	//.form-delivery__left
	&__left {
		width: 45%;
		margin: 0 auto;
		@include r(480) {
			width: 100%; } }
	//.form-delivery__right
	&__right {
		width: 45%;
		margin: 0 auto;
		@include r(480) {
			width: 100%; } }
	&__textarea {
		width: 95%;
		margin: auto;
		min-height: 155px;
		padding: 20px 25px;
		border: 1px solid $color-border;
		resize: none;
		color: $color-text-light;
		font-size: rem(18);
		@include placeholder {
			font-family: encore-300, sans-serif;
			font-size: rem(18);
			color: $color-text-light;
			letter-spacing: 0.33px; }
		@include r(480) {
			width: 100%; } }
	.sa-input__box {
		margin-bottom: 25px;
		&_05 {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			.sa-input__box {
				width: 45%;
				margin-bottom: 0;
				margin-top: 0;
				@include r(480) {
					margin-bottom: 25px; }
				@include r(320) {
					width: 100%; } } } } }
