.filter {
	position: fixed;
	top: 0;
	max-width: 425px;
	width: 100%;
	right: 0;
	bottom: 0;
	background-color: $color-bg-dark-lighten;
	overflow-y: auto;
	transform: translateX(250%);
	z-index: 999;
	@include tr(.5s);
	@include r(1024) {
		height: 100vh; }
	@include r(414) {
		max-width: 100%; }
	&.is-open {
		transform: translateX(0); }
	//.filter__wrap
	&__wrap {
		padding: 15px 0;
		background-color: #FFF;
		@include clr;
		@include r(480) {
			display: none !important; } }
	//.filter__inner
	&__inner {
		padding: 0 30px 0 50px;
		@include clr;
		@include r(375) {
			padding: 0 30px; } }
	//.filter__close
	&__close {
		width: 40px;
		height: 40px;
		@include br(3px);
		border: 1px solid $color-text-btn;
		background-color: $color-bg-dark;
		position: absolute;
		top: 18px;
		right: 30px;
		@include r(320) {
			width: 35px;
			height: 35px;
			top: 16px; } }
	//.filter__top
	&__top {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 70px;
		z-index: 10; }
	//.filter__content
	&__content {
		padding: 30px 0;
		position: fixed;
		top: 70px;
		bottom: 70px;
		left: 0;
		right: 0;
		overflow: auto;
		@include r(375) {
			padding: 15px 0; } }
	//.filter__bottom
	&__bottom {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 70px;
		border-top: 1px solid $color-bg-dark;
		z-index: 10;
		@include clr;
		@include r(375) {
			.filter__bottom {
				padding: 0 0 0 30px; } } }
	//.filter__title
	&__title {
		width: 145px;
		height: 25px;
		font-family: encore-50;
		font-size: rem(36);
		line-height: 70px;
		text-transform: uppercase;
		color: $color-text-light;
		letter-spacing: 2.61px;
		@include r(425) {
			font-family: $f-mobile-light; }
		@include r(375) {
			font-size: rem(28); } }
	//.filter__action
	&__action {
		display: inline-block;
		vertical-align: middle;
		line-height: 70px; }
	//.filter__btn
	&__btn {
		vertical-align: middle;
		margin-right: 45px;
		min-width: auto;
		padding: 13px 35px;
		@include r(768) {
			margin-right: 15px;
			min-width: auto;
			padding: 12px 35px; }
		@include r(320) {
			margin-right: 10px;
			padding: 10px 25px; } }
	//.filter__checkbox
	&__checkbox {
		input {
			&:checked {
				.filter-item__dropdown {
					display: block; } } } } }

.filter-item {
	margin-bottom: 40px;
	&__wrap {
		@include clr; }
	//.filter-item__title
	&__title {
		display: block;
		margin-bottom: 25px;
		font-family: encore-200;
		font-size: rem(18);
		color: $color-brown;
		@include r(425) {
			font-family: $f-mobile-light; } }
	//.filter-item__item
	&__item {
		&_showmore {
			display: inline-block;
			padding-right: 25px;
			position: relative;
			&::before {
				content: '\f107';
				font-family: FontAwesome;
				display: block;
				position: absolute;
				top: 0;
				right: 0; }
			&:hover {
				color: $color-brown; } } }
	//.filter-item_sorting
	&_sorting {
		display: none;
		@include r(768) {
			display: block; } }
	//.filter-item__dropdown
	&__dropdown {
		display: none;
		margin-top: 15px;
		.checkbox {
			padding-left: 27px;
			position: relative;
			&::after {
				content: '';
				display: block;
				width: 10px;
				height: 40px;
				border-bottom: 1px solid $color-text-btn;
				border-left: 1px solid $color-text-btn;
				position: absolute;
				top: -28px;
				left: -25px; }
			&:first-child {
				&::after {
					height: 15px;
					top: -3px; } }
			span {
				left: -7px; } } }
	&__list {
		&_color {
			width: 150px; } } }

//.filter-top__reset
.filter-top {
	padding: 7px 0;
	text-align: center;
	border-bottom: 1px solid $color-border;
	position: relative;
	display: flex;
	justify-content: center;

	@include r(1024) {
		text-align: left; }

	@include r(480) {
		display: none; }

	//.filter-top__wrap
	&__wrap {
		text-align: center;
		position: relative;
		background-color: #efefef;

		&.is-fixed {
			position: fixed;
			top: 60px;
			left: 0;
			width: 100%;
			z-index: 98;
			border-bottom: 1px solid $color-border;

			.filter-top {
				border-bottom: 0; } } }

	//.filter-top__inner
	&__inner {
		text-align: left;
		max-height: 400px;
		overflow: hidden; }

	//.filter-top__item
	&__item {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		padding: 2px 40px 2px 15px;
		cursor: pointer;
		position: relative;
		@include br(2px);
		@include tr(.35s);
		&::after {
			content: '\f107';
			font-family: FontAwesome;
			font-size: 16px;
			color: #b8b8b8;
			display: inline-block;
			position: absolute;
			top: 1px;
			right: 20px; }

		&:last-child {
			padding-right: 0;
			position: absolute;
			right: 0;

			&::after {
				display: none; } }

		&:hover {
			.filter-top__dropdown {
				visibility: visible;
				opacity: 1;
				-webkit-filter: none;
				filter: none;
				display: block!important;
				top: 30px;
				pointer-events: auto; } }

		&.is-active {
			color: $color-accent;
			.filter-top__dropdown {
				visibility: visible;
				opacity: 1;
				-webkit-filter: none;
				filter: none;
				display: block!important;
				top: 30px;
				pointer-events: auto; } }

		&.is-checked {
			color: $color-text-btn;
			border: 1px solid $color-brown;
			background-color: $color-brown-lighten;
			&::after {
				display: none; }
			.filter-top__dropdown {
				display: none !important; }
			.filter-top__reset {
				display: block; }
			.filter-top__choose {
				display: inline-block; } }
		&:hover {
			color: $color-accent; }

		@include r(480) {
			padding-left: 8px;
			&:first-child {
				padding-left: 0; } }
		@include r(480) {
			padding-left: 0; } }
	//.filter-top__reset
	&__reset {
		display: none;
		padding-left: 7px;
		position: absolute;
		top: 2px;
		right: 8px;
		border-left: 1px solid $color-brown;
		&::after {
			content: '\f00d';
			font-family: FontAwesome;
			color: $color-accent; } }
	//.filter-top__choose
	&__choose {
		display: none;
		padding-left: 5px; }
	//.filter-top__dropdown
	&__dropdown {
		min-width: 230px;
		max-width: 300px;

		background-color: $color-bg-darken;
		box-shadow: 0 0px 30px 1px rgba(#000, .5);
		@include beforearr($color-bg-darken, t, 12, 6);

		position: absolute;
		top: 40px;
		left: 0;
		z-index: 99;

		pointer-events: none;
		visibility: hidden;
		opacity: 0;
		filter: alpha(opacity=0);
		transition: opacity 150ms linear, top 150ms linear, bottom 150ms linear, visibility 150ms linear;


		&::before {
			left: 25px;
			margin-left: 0; }

		&_inner {
			max-height: 320px;
			padding: 18px 20px;
			overflow-y: auto;
			@include my-scroll; }

		.filter-item {
			width: 100%;
			margin: auto;
			float: none;
			text-align: left;

			.checkbox {
				font-family: encore-100, sans-serif;
				color: rgba(#fff, 0.7);

				@include r(425) {
					font-family: $f-mobile-light; }

				&:last-child {
					margin-bottom: 0; }

				&.is-disable {
					color: rgba(#fff, 0.3);
					&:hover {
						.text {
							color: rgba(#fff, 0.3); }
						.check {
							border-color: $color-text-btn; } } } } }

		//.filter-top__dropdown-item
		&-item {
			padding: 5px 10px;
			cursor: pointer;
			color: $color-text;
			white-space: nowrap;
			@include text-overflow;
			&:hover {
				background-color: $color-brown-lighten; } } }
	//.filter-top__btn
	&__btn {
		display: inline-block;
		vertical-align: middle;
		margin-right: 10px;
		padding-right: 30px;
		position: relative;
		@include tr(.35s);
		&::after {
			content: '';
			display: inline-block;
			vertical-align: middle;
			position: absolute;
			top: 7px;
			right: 0;
			@include s(filtr-btn); }
		@include r(768) {
			span {
				display: none; }
			&::after {
				top: -7px; } }
		&.is-disable {
			bottom: -100%; }
		@include r(480) {
			position: fixed;
			left: 0;
			bottom: 0;
			top: auto;
			width: 100%;
			z-index: 115;
			background-color: $color-text-btn;
			padding: 10px 0;
			text-transform: uppercase;
			text-align: center;
			color: #fff;
			&::after {
				display: none; }
			&.is-menu-open {
				@include r(1200) {
					left: 320px; }
				@include r(320) {
					left: 271px; } } } } }
