@mixin beforearr($color: #fff, $direction: t, $width: 12, $height: 6) {
	&::before {
		content: '';
		display: block;
		@include arr($width, $height, $color, $direction);
		position: absolute;
		top: -6px;
		left: 50%;
		margin-left: -6px; } }
//header
.header {
	color: $color-text;
	border-bottom: 1px solid $color-border;
	@include r(480) {
		height: 50px;
		text-align: center;
		background-color: #FFF;
		border-bottom: 1px solid $color-border;
		width: 100%;
		z-index: 99;
		top: -50px;
		transition:  top .7s ease;
		display: flex;
		justify-content: space-between;
		align-items: center;
		&.is-fixed {
			width: 100%;
			position: fixed;
			left: 0;
			top: 0; } }
	&__top-line {
		@include r(480) {
			display: none; } }
	&__bottom {
		position: relative;
		margin-bottom: 22px;
		@include clr;
		@include r(480) {
			display: none; } }
	&__social {
		margin-bottom: 30px;
		padding-bottom: 25px;
		border-bottom: 1px solid #3b3629;
		@include clr;
		.social {
			&__link {
				.fa {
					color: $color-text-light;
					@include tr(.35s); }
				&:hover {
					.fa {
						color: $color-accent-two; } } }
			&__list {
				display: inline-block;
				vertical-align: middle;
				float: right;
				text-align: right;
				@include r(375) {
					float: none; }
				&_icon {
					margin: 0;
					padding: 0; } }
			&__item {
				& + .social__item {
					margin-left: 15px; } } } } }

.city {
	display: inline-block;
	margin: 10px 0 25px 30px;
	font-family: encore-300;
	font-size: rem(14);
	color: rgba($color-text, .7);
	border-bottom: 1px dashed rgba($color-text, .7);
	position: relative;
	cursor: pointer;
	transition: color .35s ease;
	@include r(480) {
		display: none; }
	&::before, &::after {
		display: inline-block;
		font-family: fontawesome;
		position: absolute;
		top: 0; }
	&::before {
		content: '\f041';
		left: -20px; }
	&::after {
		content: '\f107';
		font-size: rem(16);
		right: -20px;
		top: -1px; }
	&:hover {
		color: $color-brown;
		border-color: $color-brown; }
	&.is-active {
		color: $color-brown;
		border-bottom-color: transparent;
		.city__inner {
			visibility: visible;
			opacity: 1;
			filter: none;
			display: block!important;
			top: 23px;
			pointer-events: auto; } }

	&__inner {
		position: absolute;
		top: 33px;
		left: -20px;
		right: -20px;
		background-color: #FFF;
		@include bs(0 0 30px 1px rgba($color-text, .5));
		z-index: 333;
		pointer-events: none;
		visibility: hidden;
		opacity: 0;
		filter: alpha(opacity=0);
		transition: opacity 150ms linear, top 150ms linear, bottom 150ms linear, visibility 150ms linear;
		&::before {
			content: '';
			display: block;
			@include arr(12,6, #fff, t);
			position: absolute;
			top: -6px;
			left: 50%;
			margin-left: -6px; } } }


.city-dropdown {
	overflow-y: auto;
	max-height: 170px;
	&__item {
		display: block;
		padding: 0 5px;
		color: rgba($color-text, .7);
		@include text-overflow;
		&:hover {
			background-color: $color-brown-lighten; } }


	&__item {
		display: block;
		line-height: 1.5; } }

.phone {
	font-family: oswald-500;
	font-size: rem(18);
	color: $color-text;
	text-transform: uppercase;
	letter-spacing: .33px; }

.callback {
	display: inline-block;
	vertical-align: middle;
	padding-top: 10px;
	position: relative;
	@include r(1200) {
		padding-left: 55px; }
	@include r(480) {
		width: auto;
		float: none;
		margin-right: 0;
		padding: 0;
		position: absolute;
		top: 20px;
		right: 35px;
		z-index: 99; }
	&__time {
		margin-left: 20px;
		color: rgba($color-text, .7);
		font-family: oswald-300;
		font-size: rem(16);
		@include r(1024) {
			margin-left: 10px; }
		@include r(992) {
			display: none; } }
	&__phone {
		padding-left: 30px;
		position: relative;
		&::before {
			content: '';
			display: inline-block;
			position: absolute;
			top: 3px;
			left: 0;
			@include sprite(phone); }
		@include r(480) {
			padding: 0;
			span {
				display: none; }
			&::before {
				top: -6px;
				left: 5px; } } }
	&__link {
		display: inline-block;
		margin-left: 35px;
		font-family: encore-300, sans-serif;
		font-size: rem(14);
		font-weight: 300;
		letter-spacing: 0.25px;
		border-bottom: 1px solid rgba($color-text, .4);
		@include r(480) {
			display: none; }
		&:hover {
			color: $color-brown;
			border-color: $color-brown; } } }

.logo {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	@include r(768) {
		img {
			width: 230px; }
		.icon {
			width: 200px; } }
	@include r(480) {
		margin: 10px auto auto;
		width: auto;
		float: none;
		margin-right: auto;
		z-index: 99;
		.icon {
			height: 32px;
			width: 150px; } }
	.icon {
		width: auto;
		height: 73px;
		@include r(1024) {
			height: 60px; } }

	//logo_mobile
	&_mobile {
		display: none;
		font-family: encore-400, sans-serif;
		font-size: rem(18);
		text-transform: uppercase;
		line-height: 50px;
		img {
			width: 150px;
			height: 50px; }
		@include r(480) {
			display: inline-block; } } }

.action {
	display: inline-block;
	vertical-align: middle;
	padding-top: 10px;
	text-align: right;

	@include r(1200) {
		text-align: center; }

	@include r(992) {
		text-align: right; }

	@include r(768) {
		text-align: center; }

	@include r(480) {
		display: block;
		padding-bottom: 20px;
		width: auto;
		float: none;
		text-align: center; }

	&__link {
		display: inline-block;
		padding: 7px 10px;
		.icon {
			@include tr(.35s);
			&:hover {
				fill: $color-accent; } } }

	&__item {
		display: inline-block;
		cursor: pointer;
		position: relative;
		&:hover {
			> .action__dropdown {
				visibility: visible;
				opacity: 1;
				filter: none;
				display: block;
				top: 47px;
				pointer-events: auto; } }

		& + & {
			margin-left: 30px;
			@include r(768) {
				margin-left: 10px; } }

		.icon {
			display: inline-block;
			width: 22px;
			height: 22px;
			fill: #606060;
			@include r(480) {
				width: 26px;
				height: 26px; }
			&-cart {
				width: 28px;
				height: 28px;
				@include r(480) {
					width: 28px;
					height: 28px; } }
			&-favorite {
				width: 24px;
				height: 24px;
				@include r(480) {
					width: 28px;
					height: 28px; } } }

		.sa-input {
			&__placeholder {
				top: 25px;
				padding-right: 15px; } } }

	&__dropdown {
		min-width: 320px;
		width: 460px;
		padding: 0 30px 35px;
		text-align: left;
		position: absolute;
		top: 57px;
		left: 0;
		z-index: 99;
		background-image: url("../img/general/dropdown-bg.png");
		background-repeat: no-repeat;
		background-position: top left;
		pointer-events: none;
		visibility: hidden;
		opacity: 0;
		filter: alpha(opacity=0);
		transition: opacity 100ms linear, top 100ms linear, bottom 100ms linear, visibility 100ms linear;
		@include beforearr(#393939, t, 22, 11);
		&::before {
			top: -11px;
			margin-left: -11px; }
		@include r(480) {
			display: none !important;
			opacity: 1;
			visibility: visible;
			filter: none;
			pointer-events: auto;
			&.user-enter, &.search-block {
				display: block !important;
				.sa-close {
					display: inline-flex !important; } } } }

	&__title {
		margin-bottom: 40px;
		padding-top: 30px;
		font-family: encore-50, sans-serif;
		font-size: rem(36);
		text-align: center;
		letter-spacing: 0.26px;
		color: $color-text;
		@include r(320) {
			font-size: rem(32);
			margin-bottom: 20px; } } }

.search {
	text-align: center;
	//search__desc
	&__desc {
		display: block;
		margin-bottom: 25px;
		color: $color-text;
		font-family: encore-300;
		font-size: rem(14);
		line-height: 18px; }
	//search__result
	&__result {
		display: none;
		max-height: 275px;
		padding-right: 10px;
		overflow-y: auto;
		@include r(480) {
			max-height: 100%;
			.cart-item {
				&__price {
					top: auto;
					bottom: 0;
					right: 0;
					transform: translateY(0); }
				&__del {
					top: 26px;
					transform: translateY(0); } } } }
	.action {
		&__dropdown {
			left: 50%;
			transform: translateX(-50%); } }
	.sa-input {
		padding-right: 40px;
		&__box {
			margin-bottom: 25px; } } }

//User
.user {
	.action {
		@include rmin(481) {
			&__dropdown {
				left: -5px;
				transform: translateX(-50%);
				&::before {
					left: 55.7%; } } }
		@include r(1200) {
			left: auto;
			transform: translateX(12%);
			right: 100%;
			&::before {
				left: auto;
				right: 32px; } } } }

//Social
.social {
	&__title {
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		font-family: encore-300, sans-serif;
		font-size: 16px;
		line-height: 18px;
		color: $color-text;
		@include r(375) {
			display: block;
			margin-bottom: 15px; } }
	&__item {
		display: inline-block;
		& + & {
			margin-left: 30px; } }
	&__list {
		&_icon {
			display: block;
			margin-bottom: 27px;
			padding-top: 10px; }

		&_communications {
			display: block;
			margin-bottom: 15px; } } }

//Auth
.auth {
	.is-active {
		color: $color-accent; }
	&__tabs {
		margin-bottom: 25px; }
	&__tab {
		font-family: encore-300, sans-serif;
		font-size: rem(16);
		color: $color-text-light;
		border-bottom: 1px dashed rgba($color-text-light, .5);
		margin-right: 35px;
		cursor: pointer;
		@include tr(.35s);
		&:last-child {
			margin-right: 0; }
		&:hover {
			color: $color-accent; } }
	&__item {
		.sa-input {
			&__placeholder {
				color: $color-text-light;
				font-family: encore-300, sans-serif;
				font-size: rem(18); } } }
	&__desc {
		display: block;
		margin: 30px 0;
		color: $color-text;
		font-size: rem(14);
		line-height: 1.3; }

	&__email {
		@include r(480) {
			display: none; } } }

.not-registered {
	margin-top: 30px;
	&__title {
		display: block;
		margin-bottom: 20px;
		font-size: rem(14);
		line-height: 1.3;
		color: $color-accent; }
	.btn {
		margin-top: 45px; } }

//Favorite
.favorite {
	&__img {
		width: 150px;
		margin: 0 auto 40px;
		img {
			margin: 0 auto; } }
	&__desc {
		display: block;
		margin-bottom: 30px;
		text-align: center;
		font-size: rem(14);
		line-height: 1.3;
		color: $color-brown; }
	&__inner {
		max-height: 300px;
		overflow-y: auto; }

	.action {
		&__dropdown {
			left: auto;
			right: 9px;
			transform: translateX(25%);
			&::before {
				left: auto;
				right: 116px; }
			@include r(1200) {
				transform: translateX(7%);
				&::before {
					right: 33px; } }
			.nicescroll-rails {
				left: 456px !important; } } } }


//Cart
.cart {
	&__inner {
		max-height: 300px;
		overflow-y: auto; }

	&__link {
		position: relative;
		span {
			width: 18px;
			height: 18px;
			line-height: 18px;
			background-color: $color-brown-lighten;
			@include br(50%);
			text-align: center;
			font-family: oswald-600;
			font-size: rem(11);
			color: $color-brown;
			position: absolute;
			top: -5px;
			right: 0; } }
	&__action {
		padding-top: 30px;
		border-top: 1px solid $color-text-btn; }
	&__summ {
		float: right;
		margin-right: 57px;
		display: inline-block;
		vertical-align: bottom;
		text-align: right;
		color: $color-accent;
		font-family: oswald-500;
		font-size: rem(24);
		line-height: 1;
		@include clr;
		span {
			display: block;
			margin-bottom: 12px;
			color: $color-brown;
			font-family: encore-400, sans-serif;
			font-size: rem(16); }
		.btn {
			vertical-align: bottom; }
		.rub {
			color: $color-accent; } }

	.action {
		&__dropdown {
			left: auto;
			right: 33px;
			transform: translateX(13%);
			&::before {
				left: auto;
				right: 35px; }
			@include r(1200) {
				transform: translateX(8%);
				&::before {
					right: 12px; } }
			.nicescroll-rails {
				left: 456px !important; } } } }

//Cart-item
.cart-item {
	margin-bottom: 30px;
	position: relative;
	@include clr;
	//cart-item__img
	&__img {
		float: left;
		width: 60px;
		height: 60px;
		margin-right: 20px; }
	//cart-item__text
	&__text {
		float: left;
		display: inline-block;
		vertical-align: middle;
		width: 150px;
		font-size: 14px;
		text-align: left;
		line-height: 1.3;
		color: $color-text-light; }
	//cart-item__articul
	&__articul {
		display: block;
		font-size: rem(13);
		font-family: oswald-400, sans-serif;
		color: $color-accent-two; }
	//cart-item__price
	&__price {
		display: inline-block;
		float: right;
		font-family: oswald-300;
		font-size: rem(16);
		line-height: 1.3;
		color: $color-accent;
		@include vy;
		right: 50px;
		.rub {
			color: $color-accent; } }
	//cart-item__del
	&__del {
		display: inline-block;
		width: 32px;
		height: 32px;
		text-align: center;
		border-radius: 3px;
		border: 1px solid $color-text-btn;
		background-color: $color-bg-dark;
		cursor: pointer;
		@include tr(.35s);
		position: absolute;
		top: 50%;
		right: 0;
		margin-top: -16px;
		.icon {
			width: 14px;
			padding-top: 8px;
			fill: $color-text;
			@include tr(.35s); }
		&:hover {
			border-color: #877c5f;
			.icon {
				fill: $color-accent; } } } }

//header-catalog
.header-catalog {
	height: 60px;
	position: fixed;
	left: 0;
	top: -150%;
	width: 100%;
	background-color: #FFF;
	z-index: 115;
	@include tr(.35s);
	@include clr;
	&.is-visible {
		top: 0; }
	&__logo {
		display: inline-block;
		vertical-align: middle;
		line-height: 60px;
		margin-right: 35px;
		.icon {
			height: 32px;
			width: 188px;
			position: relative;
			top: 10px; }
		@include r(1024) {
			margin-right: 40px; } }
	&__callback {
		display: inline-block;
		vertical-align: middle;
		float: right;
		margin-right: 60px;
		line-height: 60px;
		@include r(1024) {
			margin-right: 40px; }
		@include r(768) {
			display: none; } }
	&__menu {
		display: inline-block;
		line-height: 60px;
		margin-left: 27px;
		@include r(1024) {
			margin-left: 0; } }
	&__item {
		display: inline-block;
		vertical-align: middle;
		& + & {
			margin-left: 30px; } }
	.action {
		width: auto;
		display: inline-block;
		vertical-align: middle;
		float: right;
		padding-top: 19px;
		&__item {
			& + .action__item {
				@include r(1024) {
					margin-left: 30px; } } }
		&__dropdown {
			left: auto;
			right: -20px;
			transform: translateX(0);
			&::before {
				left: auto;
				right: 20px; }
			@include r(480) {
				display: none !important; } } }
	.user {
		.action {
			&__dropdown {
				left: auto;
				right: -20px;
				transform: translateX(0);
				&::before {
					left: auto;
					right: 20px; } } } } }
